


































































@import "@/styles/util/util.scss";

// General
#t2w-nav {
  @include transition(all 0.5s ease);
  min-height: 90px;
  .navbar-brand {
      @include transition(all 0.5s ease);
      background: url("../assets/images/logo/team2work-white.svg") no-repeat;
      width: 220px;
      height: 130px;
      position: absolute;
      top: 10px;
      z-index: -999999;
  }
  a {
    color: #FFF;
    &:hover {
      color: $color-red-t2w;
    }
  }

  // Scroll
  &.active {
    @include transition(all 0.5s ease);
    background: #FFF;
    min-height: 90px;
    .navbar-brand {
        @include transition(all 0.5s ease);
        background: url("../assets/images/logo/team2work.svg") no-repeat;
        width: 130px;
        height: 80px;
        position: absolute;
        top: 5px;
    }
    a {
      color: #000;
      &:hover {
        color: $color-red-t2w;
      }
    }
    .navbar-collapse.collapse.show {
      background: #FFF;
      margin-top: 50px;
      text-align: right;
    }
  }
}

// Large
@media (max-width: 992px) {
  #t2w-nav {
    .navbar-brand {
      width: 130px;
      top: 5px;
    }
  }
}

// Medium
@media (max-width: 768px) {
  #t2w-nav {
    background: #FFF;
    border-bottom: 1px solid #F0F0F0;
    .navbar-brand {
      background: url("../assets/images/logo/team2work.svg") no-repeat;
      width: 130px;
      top: 5px;
    }
    a {
      color: #000;
      &:hover {
        color: $color-red-t2w;
      }
    }
    .navbar-collapse.collapse.show {
      background: #FFF;
      margin-top: 50px;
      text-align: right;
    }
  }
}

// Small
@media (max-width: 576px) {
  #t2w-nav {
    background: #FFF;
    border-bottom: 1px solid #F0F0F0;
    .navbar-brand {
      background: url("../assets/images/logo/team2work.svg") no-repeat;
      width: 130px;
      top: 5px;
    }
    a {
      color: #000;
      &:hover {
        color: $color-red-t2w;
      }
    }
    .navbar-collapse.collapse.show {
      background: #FFF;
      margin-top: 50px;
      text-align: right;
    }
  }
}
