


















































































@import "@/styles/util/util.scss";
.cookie {
    position: fixed;
    overflow: hidden;
    box-sizing: border-box;
    z-index: 1;
    width: 100%;
    background: $color-black-medium;
    color: $color-white;
    padding: 1.250em 0;
}

.cookie__content > a {
    font-size: 14px;
    color: $color-white;
    text-decoration: underline;
    &:hover {
        text-decoration: underline;
    }
}

.cookie--top {
    top: 0;
    left: 0;
    right: 0;
}

.cookie--bottom {
    bottom: 0;
    left: 0;
    right: 0;
}
.cookie__button {
    cursor: pointer;
    text-align: center;
    background: $color-red-t2w;
    padding: 10px;
    border: none;
    border-radius: 0;
    &:hover {
        background: $color-red;
        color: $color-white;
    }
}
